import React from "react";
import { Button, Input, List, Segment, Header, Icon } from "semantic-ui-react";

const Todos = ({ menu }) => {
  if (menu.activeItem !== "todos") return <></>;

  return (
    <Segment placeholder>
      <Header icon>
        <Icon name="add circle" />
        Add to do list here.
      </Header>
      <Input type="text" action placeholder="e.g., collect books from library.">
        <input />
        <Button type="add">add</Button>
      </Input>

      <List bulleted>
        <List.Item>Gaining Access</List.Item>
        <List.Item>Inviting Friends</List.Item>
      </List>
    </Segment>
  );
};

export default Todos;
